
import { defineComponent } from "vue";

export default defineComponent({
  name: "my-carousel",
  data() {
    return {
      isClicked: false,
    };
  },
  methods: {
    onMouseDown() {
      this.isClicked = true;
    },
    onMouseUp() {
      this.isClicked = false;
    },
  },
});
