
import { defineComponent } from "vue";
import { Carousel, Slide } from "vue3-carousel";
import "vue3-carousel/dist/carousel.css";
import ImageContainer from "@/components/atoms/ImageContainer.vue";
import Overlay from "@/components/atoms/Overlay.vue";
import { mainData, PostItem } from "@/assets/hubmock/MainContent";

interface State {
  breakpoints: {
    [width: number]: {
      itemsToShow: number;
      snapAlign: string;
    };
  };
  mainItems: PostItem[];
}

export default defineComponent({
  name: "main-content",
  components: {
    Carousel,
    Slide,
    ImageContainer,
    Overlay,
  },
  data(): State {
    return {
      breakpoints: {
        1: {
          itemsToShow: 1.2,
          snapAlign: "start",
        },
      },
      mainItems: mainData,
    };
  },
  methods: {
    parsingTitle(title: string, idx: number) {
      return title.split("\n")[idx];
    },
  },
});
