
import { defineComponent } from "vue";
import { Carousel, Slide } from "vue3-carousel";
import "vue3-carousel/dist/carousel.css";
import { NavText, NavUrl } from "custom-type";

interface MenuItem {
  text: NavText;
  url: NavUrl;
}

interface State {
  menuItems: MenuItem[];
  selectedMenuItemIndex: number;
  showElementFixed: boolean;
  breakpoints: {
    [key: number]: {
      itemsToShow: number;
      snapAlign: string;
    };
  };
}

export default defineComponent({
  name: "header-bottom",
  components: {
    Carousel,
    Slide,
  },
  mounted() {
    document.addEventListener("scroll", this.onScrollEvent);
  },
  unmounted() {
    document.removeEventListener("scroll", this.onScrollEvent);
  },
  methods: {
    onClickMenu(idx: number) {
      this.selectedMenuItemIndex = idx;
    },
    isCurrentTab(url: string) {
      return this.$route.path === url;
    },
    onScrollEvent() {
      if (window.pageYOffset <= 48) {
        this.showElementFixed = false;
        return;
      }

      this.showElementFixed = true;
    },
  },
  data(): State {
    return {
      menuItems: [
        {
          text: "홈",
          url: "/",
        },
        {
          text: "라이프",
          url: "/life",
        },
        {
          text: "푸드",
          url: "/food",
        },
        {
          text: "여행",
          url: "/trip",
        },
        {
          text: "컬처",
          url: "/culture",
        },
        {
          text: "테크",
          url: "/tech",
        },
        {
          text: "비즈",
          url: "/biz",
        },
        {
          text: "이슈",
          url: "/issue",
        },
        {
          text: "연예",
          url: "/entertainment",
        },
      ],
      selectedMenuItemIndex: 0,
      showElementFixed: false,
      breakpoints: {
        325: {
          itemsToShow: 7.5,
          snapAlign: "start",
        },
        455: {
          itemsToShow: 9,
          snapAlign: "start",
        },
      },
    };
  },
});
